.main-block {
  // .main-block__heading

  &__heading {
  }

  // .main-block__title

  &__title {
    padding-top: rem(42);

    @media (min-width: $sm) {
      padding-top: rem(50);
    }

    span {
      color: $mainColorDark;
    }
  }

  // .main-block__subtitle

  &__subtitle {
    display: inline-block;
    margin-bottom: em(18, 12);
    font-weight: 400;
    font-size: rem(12);
    line-height: lh(16, 12);

    @media (min-width: $md) {
      margin-bottom: em(25, 12);
    }

    @media (min-width: $lg) {
      margin-bottom: em(42, 20);
      font-size: rem(20);
    }

    span {
      color: $mainColorDark;
      font-weight: 500;
    }
  }

  // .main-block__order

  &__order {
    @media (min-width: $lg) {
      display: flex;
    }
  }

  // .main-block__btn

  &__btn {
    margin-bottom: rem(18);

    @media (min-width: $lg) {
      margin-right: rem(30);
    }
  }

  // .main-block__price

  &__price {
    p:first-child {
      margin-bottom: em(5, 12);
      font-weight: 400;
      font-size: rem(12);

      @media (min-width: $lg) {
        font-size: rem(16);
      }
    }

    p:last-child {
      color: $mainColorDark;
      font-weight: 700;
      font-size: rem(16);

      @media (min-width: $md) {
        font-size: rem(18);
      }

      @media (min-width: $lg) {
        font-size: rem(24);
      }
    }
  }

  // .main-block__img

  &__img {
    position: absolute;
    top: rem(-114);
    right: rem(50);
    overflow: hidden;
    background-color: $bgColor;
    border: rem(10) solid $bgColor;
    border-radius: 50%;
    box-shadow: 0 0 rem(30) rgba(86, 86, 86, 30%);
    transform: rotate(-25deg);

    @media (min-width: $sm) {
      top: rem(-30);
    }

    @media (min-width: $md) {
      top: 0;
      right: 0;
      width: rem(306);
      height: rem(306);
    }

    @media (min-width: $xl) {
      top: rem(-100);
      width: rem(550);
      height: rem(550);
      border-width: rem(30);
    }

    @media (min-width: rem(1920)) {
      right: rem(-300);
      width: rem(700);
      height: rem(700);
    }

    @include adaptive-value("width", 306, 128, 768, 320);
    @include adaptive-value("height", 306, 128, 768, 320);
    @include adaptive-value("right", -300, 0, 1920, 1280);

    img {
    }
  }
}
