@font-face {
  font-weight: 300;
  font-family: "Montserrat";
  font-style: normal;
  src: url("../fonts/Montserrat-Light.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-weight: 400;
  font-family: "Montserrat";
  font-style: normal;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-weight: 500;
  font-family: "Montserrat";
  font-style: normal;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-weight: 700;
  font-family: "Montserrat";
  font-style: normal;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-weight: 300;
  font-family: "Comfortaa";
  font-style: normal;
  src: url("../fonts/Comfortaa-Light.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-weight: 700;
  font-family: "Comfortaa";
  font-style: normal;
  src: url("../fonts/Comfortaa-Bold.woff2") format("woff2");
  font-display: swap;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #020202;
  font-weight: 400;
  font-size: 0.75rem;
  font-family: "Montserrat", sans-serif;
  background-color: #f5faff;
}

a {
  color: inherit;
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  width: 100%;
  height: auto;
}

.page-wrapper {
  max-width: 120rem;
  margin: 0 auto;
  overflow: hidden;
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 0 0.9375rem;
}

.section {
  padding-top: 3.75rem;
}

.h1,
.h2,
.h3 {
  margin: 0;
  font-weight: 700;
  font-family: "Comfortaa", cursive;
}

.h1 {
  margin-bottom: 0.75rem;
  font-size: 1rem;
  line-height: 1.25;
}

.h2 {
  margin-bottom: 1.875rem;
  font-size: 1.25rem;
  text-align: center;
}

.disable-scroll {
  position: relative;
  overflow: hidden;
}

.disable-scroll::after {
  position: absolute;
  height: 100vh;
  background-color: rgba(2, 2, 2, 0.85);
  content: "";
  inset: 0;
}

.btn {
  display: inline-block;
  padding: 0.625rem 2.5rem;
  font-weight: 700;
  font-size: 0.875rem;
  font-family: Comfortaa, cursive;
  line-height: 1.4;
  border: none;
  border-radius: 50rem;
  cursor: pointer;
}

.btn-primary {
  color: #f5faff;
  background-color: #1b8526;
}

.btn-secondary {
  background-color: #dee6ee;
}

.btn-transparent {
  color: #1b8526;
  background-color: transparent;
  border: 1px solid #1b8526;
}

.mobile-btn {
  position: absolute;
  top: 0.125rem;
  right: 0.9375rem;
  z-index: 101;
  width: 4rem;
  height: 4rem;
  background-color: #f5faff;
  border: none;
  border-radius: 50%;
  outline: none;
  box-shadow: 0 0 1.875rem rgba(86, 86, 86, 0.3);
}

.mobile-btn.fixed {
  position: fixed;
  animation: show-nav 0.7s;
}

.mobile-btn-wrap {
  position: relative;
  top: 34%;
  left: 30%;
  width: 100%;
  height: 100%;
}

.mobile-btn-wrap span {
  position: absolute;
  display: block;
  width: 1.5625rem;
  height: 0.25rem;
  background-color: #020202;
  border-radius: 0.25rem;
  transform: rotate(0deg);
  transition: all 0.3s;
}

.mobile-btn-wrap span:nth-child(1) {
  top: 0;
}

.mobile-btn-wrap span:nth-child(2) {
  top: 0.5rem;
  left: 0.25rem;
  width: 1.25rem;
}

.mobile-btn-wrap span:nth-child(3) {
  top: 0.5rem;
  left: 0.25rem;
  width: 1.25rem;
}

.mobile-btn-wrap span:nth-child(4) {
  top: 1rem;
}

.mobile-btn-active span:nth-child(1) {
  width: 0;
}

.mobile-btn-active span:nth-child(2) {
  transform: rotate(45deg);
}

.mobile-btn-active span:nth-child(3) {
  transform: rotate(-45deg);
}

.mobile-btn-active span:nth-child(4) {
  width: 0;
}

@keyframes show-nav {
  0% {
    transform: translateY(-3.125rem);
  }

  100% {
    transform: translateY(0);
  }
}

.tabs-programs button[aria-selected=true] {
  color: #1b8526;
}

.tabs button[aria-selected=true] {
  color: #f5faff;
  background-color: #1b8526;
}

.accordeon {
  max-width: 42.5rem;
}

.accordeon-item {
  margin: 1.25rem 0;
  padding: 0.875rem 1.25rem;
  font-weight: 400;
  font-size: 0.625rem;
  line-height: 1.4;
  border-radius: 1.875rem;
  box-shadow: 0 0 1.875rem rgba(86, 86, 86, 0.3);
}

.accordeon-item__head {
  display: flex;
  align-items: center;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease-out;
}

.accordeon-item__head.active {
  margin-bottom: 0.625rem;
}

.accordeon-item__text {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out;
}

.header {
  position: relative;
  padding-top: 0.625rem;
  padding-bottom: 8rem;
}

.header::after {
  position: absolute;
  top: -17.125rem;
  right: -60%;
  z-index: -2;
  width: 32.375rem;
  height: 32.375rem;
  background-color: #64d370;
  border-radius: 50%;
  content: "";
}

.header__row {
  display: flex;
  align-items: center;
}

.header__logo {
  align-self: start;
  position: relative;
  left: 0.9375rem;
  width: 6.5rem;
  min-width: 6.5rem;
}

.header__logo img {
  position: relative;
  z-index: 2;
}

.header__logo::after {
  position: absolute;
  top: -2.125rem;
  left: -1rem;
  z-index: 1;
  width: 8.625rem;
  height: 8.625rem;
  background-color: #f5faff;
  border-radius: 50%;
  box-shadow: 0 0 1.875rem rgba(86, 86, 86, 0.3);
  content: "";
}

.header__nav,
.header__phone {
  display: flex;
  align-items: center;
  box-shadow: 0 0 1.875rem rgba(86, 86, 86, 0.3);
}

.nav {
  background-color: #f5faff;
}

.nav__item {
  display: inline-block;
}

.nav__link {
  font-weight: 400;
  white-space: nowrap;
}

.nav--active .nav__link {
  display: inline-block;
  margin-bottom: 1.25rem;
  font-weight: 700;
  font-family: Comfortaa, cursive;
  line-height: 1.375;
}

.phone {
  background-color: #f5faff;
}

.phone__link {
  color: #64d370;
  font-weight: bold;
  font-size: 0;
  white-space: nowrap;
}

.phone__icon {
  height: 1.875rem;
  fill: #64d370;
}

.main-block__title {
  padding-top: 2.625rem;
}

.main-block__title span {
  color: #1b8526;
}

.main-block__subtitle {
  display: inline-block;
  margin-bottom: 1.5em;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 1.3333333333;
}

.main-block__subtitle span {
  color: #1b8526;
  font-weight: 500;
}

.main-block__btn {
  margin-bottom: 1.125rem;
}

.main-block__price p:first-child {
  margin-bottom: 0.4166666667em;
  font-weight: 400;
  font-size: 0.75rem;
}

.main-block__price p:last-child {
  color: #1b8526;
  font-weight: 700;
  font-size: 1rem;
}

.main-block__img {
  position: absolute;
  top: -7.125rem;
  right: 3.125rem;
  overflow: hidden;
  background-color: #f5faff;
  border: 0.625rem solid #f5faff;
  border-radius: 50%;
  box-shadow: 0 0 1.875rem rgba(86, 86, 86, 0.3);
  transform: rotate(-25deg);
}

.advantages__row {
  display: grid;
  grid-column: 2.1875rem;
  grid-template-columns: repeat(auto-fit, minmax(126px, 1fr));
  row-gap: 1.25rem;
  column-gap: 2.1875rem;
}

.advantages__item {
  text-align: center;
}

.advantages__icon {
  width: 100%;
  height: 2.625rem;
  margin-bottom: 0.875rem;
  fill: #64d370;
}

.advantages__text {
  font-size: 0.5rem;
  line-height: 2;
}

.tabs {
  margin-bottom: 1.5625rem;
}

.tabs__btn {
  width: 100%;
  margin-bottom: 0.9375rem;
  padding-top: 0.6875rem;
  padding-bottom: 0.625rem;
}

.tabs-programs {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  margin-bottom: 2.375rem;
  column-gap: 0.625rem;
  row-gap: 1.25rem;
}

.tabs-programs__tab {
  background-color: transparent;
  border: none;
}

.tabs-programs__tab span {
  display: block;
}

.tabs-programs__tab span:first-child {
  margin-bottom: 0.3125rem;
  font-weight: 700;
  font-size: 0.75rem;
  font-family: Comfortaa, cursive;
  text-transform: uppercase;
}

.tabs-programs__tab span:last-child {
  font-size: 0.625rem;
}

.programs-content__order-tabs {
  margin-bottom: 2.5rem;
}

.order-tabs__header {
  margin-bottom: 1.25rem;
  padding: 1.5625rem 1.875rem;
  color: #f5faff;
  font-size: 0.625rem;
  background-color: #1b8526;
  border-radius: 1.875rem;
}

.order-tabs__header-title {
  margin-bottom: 0.8571428571em;
  font-size: 0.875rem;
  font-family: Comfortaa, cursive;
  text-transform: uppercase;
}

.order-tabs__header-title span {
  font-size: 0.75rem;
  text-transform: initial;
}

.order-tabs__header-text {
  line-height: 1.25;
}

.order-tabs__pricelist {
  margin-bottom: 1.5625rem;
  font-weight: 400;
  font-size: 0.625rem;
}

.order-tabs__btn {
  display: block;
  margin: 0 auto;
}

.pricelist-tabs__list {
  padding: 0 1.5625rem;
}

.pricelist-tabs__item {
  display: flex;
  margin-bottom: 0.625rem;
}

.pricelist-tabs__old-price {
  display: inline-block;
  margin-right: 1.375rem;
  margin-left: auto;
}

.schedule-tabs__days-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.25rem;
  padding: 1rem 1.125rem 0.8125rem;
  font-weight: 700;
  font-size: 0.875rem;
  font-family: Comfortaa, cursive;
  text-transform: uppercase;
  border-radius: 1.875rem;
  box-shadow: 0 0 1.875rem rgba(86, 86, 86, 0.3);
}

.schedule-tabs__data-list {
  margin-bottom: 1.875rem;
  padding: 1.25rem 1.875rem;
  border-radius: 1.875rem;
  box-shadow: 0 0 1.875rem rgba(86, 86, 86, 0.3);
}

.schedule-tabs__data-item {
  margin-bottom: 1.5625rem;
}

.schedule-tabs__data-text {
  margin-bottom: 0.625rem;
}

.schedule-tabs__data-text span {
  display: block;
  font-weight: 300;
  font-size: 0.75rem;
}

.schedule-tabs__data-ration {
  margin-right: 1.4285714286em;
  font-weight: 400;
  font-size: 0.875rem;
}

.schedule-tabs__data-weight {
  display: none;
}

.swiper-wrapper {
  display: flex;
}

.swiper-slide {
  text-align: center;
  background: #fff;
}

.swiper-slide {
  overflow: hidden;
  border-radius: 30px;
  box-shadow: 0 0 20px 0 0 1.875rem rgba(86, 86, 86, 0.3);
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  user-select: none;
  pointer-events: none;
}

.order {
  padding-bottom: 2.875rem;
}

.order__col {
  max-width: 26.125rem;
  margin: 0 auto;
}

.order__text {
  margin-bottom: 1.25rem;
  font-weight: 400;
  text-align: center;
}

.form__group {
  position: relative;
  margin-bottom: 1.875rem;
}

.form__group_checkbox {
  position: relative;
  display: flex;
}

.form__label {
  display: inline-block;
  margin-bottom: 0.625rem;
  margin-left: 1.25rem;
  font-weight: 700;
  font-size: 1.125rem;
  font-family: Comfortaa, cursive;
  cursor: pointer;
}

.form__input {
  width: 100%;
  padding: 0.75rem 1.375rem 0.625rem;
  font-size: 1rem;
  line-height: 1.375;
  background-color: #dee6ee;
  border: none;
  border-radius: 1.875rem;
  outline-color: #64d370;
}

.form__checkbox-text {
  margin-left: 2.875rem;
  font-weight: 700;
  font-size: 0.875rem;
  font-family: Comfortaa, cursive;
}

.form__checkbox-text span {
  color: #1b8526;
}

.form__checkbox {
  display: none;
}

.form__label-checkbox {
  position: absolute;
  top: -0.1875rem;
  left: 0;
  width: 1.625rem;
  height: 1.625rem;
  font-size: 0;
  background-color: #dee6ee;
  border-radius: 50%;
  cursor: pointer;
}

.form__label-checkbox::after {
  position: absolute;
  top: 27%;
  left: 27%;
  width: 0.75rem;
  height: 0.75rem;
  background-color: transparent;
  border-radius: 50%;
  content: "";
}

.form__checkbox:checked + .form__label-checkbox::after {
  background-color: #64d370;
}

.form__text-or {
  margin-bottom: 0.625rem;
  font-weight: 400;
  font-size: 1rem;
  text-align: center;
}

.form__btn-tranparent {
  margin-bottom: 0.625rem;
}

.form__btn-tranparent,
.form__btn-primary {
  width: 100%;
}

.form__btn-primary {
  margin-bottom: 3.75rem;
}

.footer {
  margin-bottom: 3.125rem;
  padding: 2.5rem 0;
  box-shadow: 0 0 1.875rem rgba(86, 86, 86, 0.3);
}

.footer__row {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.footer__logo {
  max-width: 13.1875rem;
  margin-bottom: 1.5625rem;
}

.info-footer__social {
  margin-bottom: 0.75rem;
}

.social-footer__list {
  display: flex;
}

.social-footer__item:not(:first-child) {
  margin-left: 0.625rem;
}

.social-footer__icon {
  display: block;
  width: 1.875rem;
  height: 1.875rem;
  transition: fill 0.3s;
}

.phone__link {
  color: #1b8526;
  font-weight: 700;
  font-size: 0.875rem;
}

@media (min-width: 30rem) {
  .section {
    padding-top: 5rem;
  }

  .header {
    padding-bottom: 3.125rem;
  }

  .header::after {
    top: -13.75rem;
  }

  .header {
    padding-top: 0.625rem;
  }

  .header__nav,
  .header__phone {
    padding-top: 0.625em;
    padding-bottom: 0.625em;
    border-radius: 11.875rem;
  }

  .nav {
    padding-right: 1.25rem;
  }

  .nav__item:not(:last-child) {
    margin-right: 0.625rem;
  }

  .phone {
    margin-right: 5rem;
    margin-left: auto;
    padding-right: 1.25rem;
    padding-left: 1.25rem;
  }

  .phone__link {
    font-size: 0.625rem;
  }

  .phone__icon {
    display: none;
  }

