.header {
  position: relative;
  padding-top: rem(10);
  padding-bottom: rem(128);

  @media (min-width: $sm) {
    padding-bottom: rem(50);
  }

  @media (min-width: $xl) {
    padding-bottom: rem(180);
  }

  &::after {
    position: absolute;
    top: rem(-274);
    right: -60%;
    z-index: -2;
    width: rem(518);
    height: rem(518);
    background-color: $mainColor;
    border-radius: 50%;
    content: "";

    @media (min-width: $sm) {
      top: rem(-220);
    }

    @media (min-width: rem(568)) {
      left: 45vw;
    }

    @media (min-width: $md) {
      left: 50vw;
    }

    @media (min-width: $lg) {
      left: 58vw;
    }

    @media (min-width: $xl) {
      top: rem(-100);
      left: 50%;
    }

    @include adaptive-value("width", 1290, 518, 1600, 320, 1);
    @include adaptive-value("height", 1290, 518, 1600, 320, 1);
  }

  @media (min-width: $sm) {
    padding-top: rem(10);
  }

  @media (min-width: $md) {
  }

  @media (min-width: $xl) {
    padding-top: rem(25);
  }

  // .header__row

  &__row {
    display: flex;
    align-items: center;

    @media (max-width: rem(480.98)) {
      &::after {
        position: absolute;
        top: rem(16);
        left: rem(30);
        z-index: -1;
        width: 84%;
        height: rem(34);
        background-color: $bgColor;
        content: "";
      }
    }
  }

  // .header__logo

  &__logo {
    align-self: start;
    position: relative;
    left: rem(15);
    width: rem(104);
    min-width: rem(104);

    @media (min-width: $md) {
      left: 0;
      min-width: rem(90);
    }

    @media (min-width: $lg) {
      min-width: rem(104);
    }

    @media (min-width: $xl) {
      width: rem(184);
    }

    img {
      position: relative;
      z-index: 2;
    }

    &::after {
      position: absolute;
      top: rem(-34);
      left: rem(-16);
      z-index: 1;
      width: rem(138);
      height: rem(138);
      background-color: $bgColor;
      border-radius: 50%;
      box-shadow: $shadow;
      content: "";

      @media (min-width: $md) {
        left: rem(-25);
      }

      @media (min-width: $lg) {
        width: rem(150);
        height: rem(150);
      }

      @media (min-width: $xl) {
        top: rem(-84);
        left: rem(-60);
        width: rem(264);
        height: rem(264);
      }
    }
  }

  // .header__nav

  &__nav {
    @media (min-width: $md) {
      margin-right: rem(10);
    }

    @media (min-width: $lg) {
      margin-right: rem(20);
    }
  }

  &__nav,
  &__phone {
    display: flex;
    align-items: center;

    // height: rem(52);
    
    box-shadow: $shadow;

    @media (min-width: $sm) {
      padding-top: em(10);
      padding-bottom: em(10);
      border-radius: rem(190);
    }

    @media (min-width: $md) {
    }

    @media (min-width: $xl) {
      padding-top: em(16);
      padding-bottom: em(16);
    }
  }
}

.logo {
  // .logo__img

  &__img {
  }
}

.nav {
  background-color: $bgColor;

  @media (max-width: rem(767.98)) {
    position: absolute;
    top: rem(-10);
    left: 0;
    z-index: 5;
    width: 100%;
    padding: rem(40) rem(33) rem(100);
    border-radius: 0 0 rem(50) rem(50);
    transform: translateY(-100%);
    transition: transform $transition;

    &--active {
      transform: translateY(0);
    }
  }

  @media (min-width: $sm) {
    // display: none;
    padding-right: rem(20);
  }

  @media (min-width: $md) {
    display: block;
    height: initial;
    padding-left: rem(36);
  }
  
  @media (min-width: $lg) {
    padding-right: rem(40);
    padding-left: rem(68);
  }

  @media (min-width: $xl) {
    padding-right: rem(40);
    padding-left: rem(85);
  }

  // .nav__list

  &__list {
    @media (max-width: rem(767.98)) {
      display: flex;
      flex-direction: column;
    }

    @media (min-width: $md) {
      // padding-left: rem(52);
      display: flex;
    }

    @media (min-width: $xl) {
      // padding-left: rem(44);
    }
  }

  

  // .nav__item

  &__item {
    display: inline-block;

    // font-size: 0;

    &:not(:last-child) {
      @media (min-width: $sm) {
        margin-right: rem(10);
      }

      @media (min-width: $md) {
        margin-right: rem(15);
      }

      @media (min-width: $lg) {
        margin-right: rem(28);
      }

      @media (min-width: $xl) {
        margin-right: rem(40);
      }
    }
  }

  // .nav__link

  &__link {
    font-weight: 400;
    white-space: nowrap;

    @media (min-width: $md) {
      font-size: rem(10);
    }

    @media (min-width: $lg) {
      font-size: rem(12);
    }

    @media (min-width: $xl) {
      font-size: rem(14);
    }

    @media (hover: hover) {
      &:hover {
        font-weight: 500;
      }
    }

    // &::before {
    //   display: block;
    //   height: 0;
    //   overflow: hidden;
    //   font-weight: bold;
    //   visibility: hidden;
    //   content: attr(title);
    // }
  }

  &--active &__link {
    display: inline-block;
    margin-bottom: rem(20);
    font-weight: 700;
    font-family: Comfortaa, cursive;
    line-height: lh(22);
  }
}

.phone {
  background-color: $bgColor;

  // transition: transform 0.3s;

  @media (max-width: rem(479.98)) {
    position: absolute;
    top: rem(2);
    right: rem(88);
    width: rem(64);
    height: rem(64);
    border-radius: 50%;
  }

  @media (min-width: $sm) {
    margin-right: rem(80);
    margin-left: auto;
    padding-right: rem(20);
    padding-left: rem(20);
  }

  @media (min-width: $md) {
    margin: 0;
  }

  @media (min-width: $lg) {
    padding-right: rem(22);
    padding-left: rem(22);
  }

  @media (min-width: $xl) {
    padding-right: rem(30);
    padding-left: rem(30);
  }

  // .phone__link

  &__link {
    color: $mainColor;
    font-weight: bold;
    font-size: 0;
    white-space: nowrap;

    @media (min-width: $sm) {
      font-size: rem(10);
    }

    @media (min-width: $md) {
    }

    @media (min-width: $lg) {
      font-size: rem(12);
    }

    @media (min-width: $xl) {
      font-size: rem(20);
    }
  }

  &__icon {
    height: rem(30);
    fill: $mainColor;

    @media (min-width: $sm) {
      display: none;
    }
  }
}

