.accordeon {
  max-width: rem(680);

  &-item {
    margin: rem(20) 0;
    padding: rem(14) rem(20);
    font-weight: 400;
    font-size: rem(10);
    line-height: lh(14, 10);
    border-radius: $radius;
    box-shadow: $shadow;

    @media (min-width: $md) {
      font-size: rem(16);
    }
  }

  &-item__head {
    display: flex;
    align-items: center;
    font-weight: 500;
    cursor: pointer;
    transition: all $transition ease-out;
  }

  &-item__head.active {
    margin-bottom: rem(10);
  }

  &-item__text {
    max-height: 0;
    overflow: hidden;
    transition: max-height $transition ease-out;
  }
}
