.order {
  padding-bottom: rem(46);

  @media (min-width: $md) {
    padding-bottom: rem(86);
  }

  @media (min-width: $lg) {
    padding-bottom: 0;
  }

  // .order__row

  &__row {
    @media (min-width: $lg) {
      display: grid;
      grid-template-columns: $col-5 $col-7;
    }
  }

  // .order__col

  &__col {
    max-width: rem(418);
    margin: 0 auto;

    @media (min-width: $lg) {
      max-width: initial;
      margin: initial;
      margin-right: rem(40);
    }

    @media (min-width: $xl) {
      margin-right: rem(60);
    }
  }

  // .order__title

  &__title {
  }

  // .order__text

  &__text {
    margin-bottom: rem(20);
    font-weight: 400;
    text-align: center;
  }

  // .order__form

  &__form {
  }
}

.form {
  // .form__group

  &__group {
    position: relative;
    margin-bottom: rem(30);
  }

  &__group_checkbox {
    position: relative;
    display: flex;
  }

  // .form__label

  &__label {
    display: inline-block;
    margin-bottom: rem(10);
    margin-left: rem(20);
    font-weight: 700;
    font-size: rem(18);
    font-family: Comfortaa, cursive;
    cursor: pointer;
  }

  // .form__label-name

  &__label-name {
    
  }

  // .form__input

  &__input {
    width: 100%;
    padding: rem(12) rem(22) rem(10);
    font-size: rem(16);
    line-height: lh(22);
    background-color: $gray;
    border: none;
    border-radius: $radius;
    outline-color: $mainColor;
  }

  // .form__label-phone

  &__label-phone {
  }

 

  // .form__input-discount

  &__input-discount {
  }

  // .form__checkbox-text

  &__checkbox-text {
    margin-left: rem(46);
    font-weight: 700;
    font-size: rem(14);
    font-family: Comfortaa, cursive;

    @media (min-width: $lg) {
      font-size: rem(16);
    }

    span {
      color: $mainColorDark;
    }
  }

  // .form__checkbox

  &__checkbox {
    display: none;
  }

  // .form__label-checkbox

  &__label-checkbox {
    position: absolute;
    top: rem(-3);
    left: 0;
    width: rem(26);
    height: rem(26);
    font-size: 0;
    background-color: $gray;
    border-radius: 50%;
    cursor: pointer;

    &::after {
      position: absolute;
      top: 27%;
      left: 27%;
      width: rem(12);
      height: rem(12);
      background-color: transparent;
      border-radius: 50%;
      content: "";
    }
  }

  &__checkbox:checked + &__label-checkbox::after {
    background-color: $mainColor;
  }
  

  // .form__text-or

  &__text-or {
    margin-bottom: rem(10);
    font-weight: 400;
    font-size: rem(16);
    text-align: center;
  }

  // .form__btn-tranparent

  &__btn-tranparent {
    margin-bottom: rem(10);
  }

  &__btn-tranparent,
  &__btn-primary {
    width: 100%;

    @media (min-width: $sm) {
      display: block;
      width: 80%;
      margin-right: auto;
      margin-left: auto;
    }

    @media (min-width: $xl) {
      padding-right: 0;
      padding-left: 0;
    }
  }

  // .form__btn-primary {

  &__btn-primary {
    margin-bottom: rem(60);

    @media (min-width: $md) {
      margin-bottom: rem(100);
    }
  }
}

