.programs {
}

.tabs {
  margin-bottom: rem(25);

  @media (min-width: $md) {
    display: flex;
  }

  // .tabs__btn

  &__btn {
    width: 100%;
    margin-bottom: rem(15);
    padding-top: rem(11);
    padding-bottom: rem(10);

    @media (min-width: $md) {
      width: 50%;
      font-size: rem(14);

      &:last-child {
        margin-left: rem(12);
      }
    }

    @media (min-width: $xl) {
      &:last-child {
        margin-left: rem(20);
      }
    }
  }
}

.btn-primary {
}

.tabs-content {
  // .tabs-content__item

  &__item {
  }
}

.tabs-programs {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  margin-bottom: rem(38);
  column-gap: rem(10);
  row-gap: rem(20);

  // .tabs-programs__tab

  &__tab {
    background-color: transparent;
    border: none;

    span {
      display: block;
    }

    span:first-child {
      margin-bottom: rem(5);
      font-weight: 700;
      font-size: rem(12);
      font-family: Comfortaa, cursive;
      text-transform: uppercase;

      @media (min-width: $xl) {
        margin-bottom: rem(10);
        font-size: rem(18);
      }
    }

    span:last-child {
      font-size: rem(10);

      @media (min-width: $xl) {
        font-size: rem(14);
      }
    }
  }
}

.tabs-programs-content {
}

.programs-content {
  // .programs-content__item-wrapper

  &__item-wrapper {
    @media (min-width: $md) {
      display: grid;
      grid-template-columns: $col-4 $col-8;
      column-gap: rem(12);
    }

    @media (min-width: $xl) {
      column-gap: rem(20);
    }
  }

  // .programs-content__order-tabs

  &__order-tabs {
    margin-bottom: rem(40);
  }

  // .programs-content__schedule-tabs

  &__schedule-tabs {
  }
}

.order-tabs {
  &__header {
    margin-bottom: rem(20);
    padding: rem(25) rem(30);
    color: $bgColor;
    font-size: rem(10);
    background-color: $mainColorDark;
    border-radius: rem(30);

    @media (min-width: $md) {
      margin-bottom: rem(30);
    }
  }

  // .order-tabs__header-title

  &__header-title {
    margin-bottom: em(12, 14);
    font-size: rem(14);
    font-family: Comfortaa, cursive;
    text-transform: uppercase;

    @media (min-width: $xl) {
      margin-bottom: em(20, 25);
      font-size: rem(25);
    }

    span {
      font-size: rem(12);
      text-transform: initial;

      @media (min-width: $xl) {
        font-size: rem(18);
      }
    }
  }

  // .order-tabs__text

  &__header-text {
    line-height: lh(20, 16);

    @media (min-width: $xl) {
      font-size: rem(16);
    }
  }

  // .order-tabs__pricelist

  &__pricelist {
    margin-bottom: rem(25);
    font-weight: 400;
    font-size: rem(10);

    @media (min-width: $md) {
      margin-bottom: rem(30);
    }

    @media (min-width: $xl) {
      margin-bottom: rem(50);
    }
  }

  // .order-tabs__btn

  &__btn {
    display: block;
    margin: 0 auto;
  }
}

.pricelist-tabs {
  // .pricelist-tabs__list

  &__list {
    padding: 0 rem(25);

    @media (min-width: $md) {
      padding: 0;
    }
  }

  // .pricelist-tabs__item

  &__item {
    display: flex;
    margin-bottom: rem(10);

    @media (min-width: $xl) {
      margin-bottom: rem(15);
      font-size: rem(15);
    }
  }

  // .pricelist-tabs__unit
  &__unit {
    
  }

  // .pricelist-tabs__old-price
  &__old-price {
    display: inline-block;
    margin-right: rem(22);
    margin-left: auto;
  }

  // .pricelist-tabs__price
  &__price {
  }
}

.schedule-tabs {
  // .schedule-tabs__days-list

  &__days-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: rem(20);
    padding: rem(16) rem(18) rem(13);
    font-weight: 700;
    font-size: rem(14);
    font-family: Comfortaa, cursive;
    text-transform: uppercase;
    border-radius: rem(30);
    box-shadow: $shadow;

    @media (min-width: $md) {
      padding: rem(12) rem(30) rem(9);

      & ~ .order-tabs__btn {
        display: none;
      }
    }

    @media (min-width: $xl) {
      padding: rem(15) rem(50) rem(13);
    }
  }

  // .schedule-tabs__days-item

  &__days-item {
    @media (min-width: $xl) {
      font-size: rem(20);
    }
  }

  // .schedule-tabs__data-list

  &__data-list {
    margin-bottom: rem(30);
    padding: rem(20) rem(30);
    border-radius: rem(30);
    box-shadow: $shadow;

    @media (min-width: $xl) {
      padding: rem(30) rem(50);
    }
  }

  // .schedule-tabs__data-item

  &__data-item {
    margin-bottom: rem(25);

    @media (min-width: $sm) {
      display: flex;
      justify-content: space-between;
    }
  }

  // .schedule-tabs__data-text

  &__data-text {
    margin-bottom: rem(10);

    @media (min-width: 568px) {
      &:first-child {
        margin-right: rem(30);
      }
    }

    @media (min-width: $xl) {
      &:first-child {
        margin-right: rem(50);
      }
    }

    span {
      display: block;
      font-weight: 300;
      font-size: rem(12);

      @media (min-width: $xl) {
        font-size: rem(18);
      }
    }
  }

  // .schedule-tabs__data-ration

  &__data-ration {
    margin-right: em(20, 14);
    font-weight: 400;
    font-size: rem(14);

    @media (min-width: 568px) {
      display: block;
      margin-right: 0;
      margin-bottom: rem(5);
    }

    @media (min-width: $md) {
    }

    @media (min-width: $xl) {
      margin-bottom: rem(10);
      font-size: rem(20);
    }
  }

  &__data-weight {
    display: none;

    @media (min-width: rem(568)) {
      display: block;
      margin-left: auto;
      font-weight: 300;
      font-size: rem(12);
    }

    @media (min-width: $xl) {
      font-size: rem(18);
    }
  }
}
