.mobile-btn {
  position: absolute;
  top: rem(2);
  right: rem(15);
  z-index: 101;
  width: rem(64);
  height: rem(64);
  background-color: $bgColor;
  border: none;
  border-radius: 50%;
  outline: none;
  box-shadow: $shadow;

  @media (min-width: $md) {
    display: none;
  }
}

.mobile-btn.fixed {
  position: fixed;
  animation: show-nav 0.7s;
}

.mobile-btn-wrap {
  position: relative;
  top: 34%;
  left: 30%;
  width: 100%;
  height: 100%;
}

.mobile-btn-wrap span {
  position: absolute;
  display: block;
  width: rem(25);
  height: rem(4);
  background-color: $textColor;
  border-radius: rem(4);
  transform: rotate(0deg);
  transition: all $transition;
}

.mobile-btn-wrap span:nth-child(1) {
  top: 0;
}

.mobile-btn-wrap span:nth-child(2) {
  top: rem(8);
  left: rem(4);
  width: rem(20);
}

.mobile-btn-wrap span:nth-child(3) {
  top: rem(8);
  left: rem(4);
  width: rem(20);
}

.mobile-btn-wrap span:nth-child(4) {
  top: rem(16);
}

.mobile-btn-active span:nth-child(1) {
  width: 0;
}

.mobile-btn-active span:nth-child(2) {
  transform: rotate(45deg);
}

.mobile-btn-active span:nth-child(3) {
  transform: rotate(-45deg);
}

.mobile-btn-active span:nth-child(4) {
  width: 0;
}

@keyframes show-nav {
  0% {
    transform: translateY(rem(-50));
  }

  100% {
    transform: translateY(0);
  }
}
