.btn {
  display: inline-block;
  padding: rem(10) rem(40);
  font-weight: 700;
  font-size: rem(14);
  font-family: Comfortaa, cursive;
  line-height: lh(19.6, 14);
  border: none;
  border-radius: 50rem;
  cursor: pointer;

  &-primary {
    color: $bgColor;
    background-color: $mainColorDark;
  }

  &-secondary {
    background-color: $gray;
  }

  &-transparent {
    color: $mainColorDark;
    background-color: transparent;
    border: 1px solid $mainColorDark;
  }

  &:hover {
    // background-color: $mainColorDark;
  }

  @media (min-width: $md) {
    font-size: rem(16);
  }

  @media (min-width: $xl) {
    padding: rem(14) rem(68);
    font-size: rem(18);
  }
}
