.footer {
  margin-bottom: rem(50);
  padding: rem(40) 0;
  box-shadow: $shadow;

  // .footer__row

  &__row {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    @media (min-width: $md) {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  // .footer__nav

  &__nav {
    @media (max-width: 767.98px) {
      display: none;
    }
  }

  // .footer__logo

  &__logo {
    max-width: rem(211);
    margin-bottom: rem(25);
  }

  // .footer__info

  &__info {
  }
}

.nav-footer {
  // .nav-footer__list

  &__list {
    @media (min-width: $md) {
      text-align: left;
    }
  }

  // .nav-footer__item

  &__item {
    @media (hover: hover) {
      &:hover {
        text-decoration: underline;
      }
    }
    

    @media (min-width: $md) {
      margin-bottom: rem(12);
      font-weight: 700;
      font-size: rem(14);
      font-family: Comfortaa, cursive;
    }

    @media (min-width: $lg) {
      margin-bottom: rem(18);
      font-size: rem(20);
    }
  }

  // .nav-footer__link

  &__link {
  }
}

.info-footer {
  @media (min-width: $md) {
    text-align: right;
  }

  // .info-footer__conditions

  &__conditions {
   
  }

  &__conditions,
  &__faq {
    @media (max-width: 767.98px) {
      display: none;
    }

    @media (min-width: $md) {
      margin-bottom: rem(14);
      font-weight: 700;
      font-size: rem(14);
      font-family: Comfortaa, cursive;
    }

    @media (min-width: $lg) {
      margin-bottom: rem(18);
      font-size: rem(20);
    }

    @media (hover: hover) {
      &:hover {
        text-decoration: underline;
      }
    }
  }

  // .info-footer__faq

  &__faq {
    @media (min-width: $md) {
      text-transform: uppercase;
    }
  }

  // .info-footer__social

  &__social {
    margin-bottom: rem(12);

    @media (min-width: $lg) {
      margin-bottom: rem(20);
    }
  }

  // .info-footer__phone

  &__phone {
  }
}

.social-footer {
  // .social-footer__list

  &__list {
    display: flex;

    @media (min-width: $md) {
      justify-content: flex-end;
    }
  }

  // .social-footer__item

  &__item {
    &:not(:first-child) {
      margin-left: rem(10);
    }
  }

  // .social-footer__link

  &__link {
    
  }

  // .social-footer__icon

  &__icon {
    display: block;
    width: rem(30);
    height: rem(30);
    transition: fill $transition;

    @media (hover: hover) {
      &:hover {
        fill: $mainColorDark;
      }
    }
  }
}

.phone {
  // .phone__link

  &__link {
    color: $mainColorDark;
    font-weight: 700;
    font-size: rem(14);

    @media (min-width: $lg) {
      font-size: rem(20);
    }
  }
}
