* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  // min-height: fill-available;
  color: $textColor;
  font-weight: 400;
  font-size: rem(12);
  font-family: $fontFamily;
  background-color: $bgColor;

  @media (min-width: $md) {
    font-weight: 500;
    font-size: $fontSize;
  }
}

a {
  color: inherit;
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  // vertical-align: middle;
}

.page-wrapper {
  max-width: rem(1920);
  margin: 0 auto;
  overflow: hidden;
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 0 rem(15);

  @media (min-width: $md) {
    max-width: rem(720);
  }

  @media (min-width: $lg) {
    max-width: rem(900);
  }

  @media (min-width: $xl) {
    max-width: rem(1180);
  }
}

.section {
  padding-top: rem(60);
  
  @media (min-width: $sm) {
    padding-top: rem(80);
  }

  @media (min-width: $xl) {
    padding-top: rem(100);
  }
}

.h1,
.h2,
.h3 {
  margin: 0;
  font-weight: 700;
  font-family: $secondFontFamily, cursive;
}

.h1 {
  margin-bottom: rem(12);
  font-size: rem(16);
  line-height: lh(20);

  @media (min-width: $md) {
    font-size: rem(18);
  }

  @media (min-width: $xl) {
    margin-bottom: rem(18);
    font-size: rem(40);
  }
}

.h2 {
  margin-bottom: rem(30);
  font-size: rem(20);
  text-align: center;

  @media (min-width: $md) {
    font-size: rem(25);
  }

  @media (min-width: $xl) {
    margin-bottom: rem(60);
    font-size: rem(30);
  }
}

.disable-scroll {
  position: relative;
  overflow: hidden;

  &::after {
    position: absolute;
    height: 100vh;
    background-color: rgba(2, 2, 2, 85%);
    content: "";
    inset: 0;
  }
}
