.advantages {
  // .advantages__row

  &__row {
    display: grid;
    grid-column: rem(35);
    grid-template-columns: repeat(auto-fit, minmax(126px, 1fr));
    row-gap: rem(20);
    column-gap: rem(35);

    @media (min-width: $sm) {
      // grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
      padding: rem(20) rem(20);
      background-color: rgba($bgColor, 0.8);
      border-radius: rem(30);
    }

    @media (min-width: $md) {
      grid-template-columns: repeat(auto-fit, minmax(90px, 1fr));
      column-gap: rem(10);
    }

    @media (min-width: $lg) {
      column-gap: rem(26);
    }
  }

  // .advantages__item

  &__item {
    text-align: center;
  }

  // .advantages__icon

  &__icon {
    width: 100%;
    height: rem(42);
    margin-bottom: rem(14);
    fill: $mainColor;

    @media (min-width: $md) {
      height: rem(36);
    }

    @media (min-width: $lg) {
      height: rem(46);
    }

    @media (min-width: $xl) {
      height: rem(68);
      margin-bottom: rem(20);
    }
  }

  // .advantages__text

  &__text {
    font-size: rem(8);
    line-height: lh(16, 8);
    
    @media (min-width: $lg) {
      font-size: rem(10);
    }

    @media (min-width: $xl) {
      font-size: rem(12);
    }
  }
}
